import { Component, OnInit } from "@angular/core";
import { AuthService } from ".././auth.service";
import { AngularFireAuth } from "@angular/fire/auth";

@Component({
  selector: "app-template",
  templateUrl: "./template.component.html",
  styleUrls: ["./template.component.css"]
})
export class TemplateComponent implements OnInit {
  selectedIndex = 0;
  maxNumberOfTabs = 2;
  isLoggedIn: Boolean = true;
  userData: any;
  boolean: boolean;
  isLoading: boolean = false;
  newUser:boolean = false;
  private _user: firebase.User;
  public get user(): firebase.User {
    return this._user;
  }
  public set user(value: firebase.User) {
    this._user = value;
  }
  constructor(
    public authService: AuthService,
    public afAuth: AngularFireAuth
  ) {}

  ngOnInit() {
    this.afAuth.authState.subscribe(user => {
      if (user) {
        this.userData = user;
        localStorage.setItem("user", JSON.stringify(this.userData));
        this.LoggedIn();
      } else {
        localStorage.setItem("user", null);
        JSON.parse(localStorage.getItem("user"));
        this.LoggedOut();
      }
    });
  }
  private LoggedIn() {
    this.isLoggedIn = true;
  }
  private LoggedOut() {
    this.isLoggedIn = false;
  }

  facebookLogin() {
    this.isLoading = true;

    this.authService.FacebookAuth().then(data => {
      this.isLoading = false;
    });
  }

  googleLogin() {
    this.isLoading = true;

    this.authService.GoogleAuth().then(data => {
      this.isLoading = false;
    });
  }

  signIn(email, pass) {
    console.log(email + pass);
    this.isLoading = true;

    this.authService.SignIn(email, pass).then(data => {
      this.isLoading = false;
    });
  }

  signUp(email, pass) {
    this.isLoading = true;

    this.authService.SignUp(email, pass).then(data => {
      this.isLoading = false;
    });
  }

  NewUser()
  {
    this.newUser= true;
  }

  OldUser()
  {
    this.newUser= false;
  }
  
}
