import { Injectable, EventEmitter } from '@angular/core';
import {
  AngularFirestore,
  AngularFirestoreCollection
} from "@angular/fire/firestore";

import { agentSignup } from '../../../../../../../Model/agentSignup';

@Injectable({
  providedIn: 'root'
})
export class AgentSelectionService {
  customersRef: any;
  propertiesRef: any;
  return: any;
 
  //private dbPathBuyer = "matchesBuyer"; 
  dataSavedEventEmitter = new EventEmitter<string>();

  constructor(private db: AngularFirestore) { 
  
    //Buyer Database
     this.customersRef = db.collection("agentSignup");
     
  }
  //Fetching Buyer Detail
  getBuyerProperties(uid): Promise<any> {
    return this.customersRef
      .doc(uid)
      .collection("agents")
      .ref.get();
  }

  
}
