import { Component, OnInit } from "@angular/core";
import { MatchesService } from "./buyerMatchListing.service";
import { StateServiceService } from "../../.././../.././state-service.service";
import { listingBuyer } from "../../../../../Model/listingBuyer";

@Component({
  selector: "app-buyerMatchListing",
  templateUrl: "./buyerMatchListing.component.html",
  styleUrls: ["./buyerMatchListing.component.css"],
})
export class BuyerMatcheListingComponent implements OnInit {
  return: any;
  userData: any;
  isLoggedIn: boolean;
  isLoading: boolean = false;
  user: any;
  uid: any;
  propertyDetails = [];
  matchedProperties = [];
  unmatchedProperties = [];
  distanceInKm;

  constructor(
    private MatchesService: MatchesService,
    private stateService: StateServiceService
  ) {}

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem("user"));
    this.uid = this.user.uid;

    // Fetch details
    this.MatchesService.getSellerProperties(this.uid).subscribe((ref) => {
      ref.forEach((item) => {
        console.log("from the db " + item.data().Lookingpostcode);
        console.log(
          "from the localStorage " +
            JSON.stringify(this.stateService.listingBuyer.Lookingpostcode)
        );
        if (
          item.data().Lookingpostcode ==
          this.stateService.listingBuyer.Lookingpostcode
        ) {
          this.matchedProperties.push(item.data()); //Use this object to populate html
        } else if (item.data().latitude && item.data().longitude) {
          this.distanceInKm = this.getDistanceFromLatLonInKm(
            this.stateService.listingBuyer.latitude,
            this.stateService.listingBuyer.longitude,
            item.data().latitude,
            item.data().longitude
          );
          this.unmatchedProperties.push({
            detail: item.data(),
            distance: this.distanceInKm,
          });
        } else {
          this.unmatchedProperties.push({ detail: item.data() });
        }
      });

      this.unmatchedProperties.sort(this.compare);
    });
  }

  getDistanceFromLatLonInKm(lat1, lon1, lat2, lon2) {
    var R = 6371; // Radius of the earth in km
    var dLat = this.deg2rad(lat2 - lat1); // deg2rad below
    var dLon = this.deg2rad(lon2 - lon1);
    var a =
      Math.sin(dLat / 2) * Math.sin(dLat / 2) +
      Math.cos(this.deg2rad(lat1)) *
        Math.cos(this.deg2rad(lat2)) *
        Math.sin(dLon / 2) *
        Math.sin(dLon / 2);
    var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    var d = R * c; // Distance in km
    return d;
  }

  deg2rad(deg) {
    return deg * (Math.PI / 180);
  }

  compare(a, b) {
    const distA = a.distance;
    const distB = b.distance;

    let comparison = 0;
    if (distA > distB) {
      comparison = 1;
    } else if (distA < distB) {
      comparison = -1;
    }
    return comparison;
  }
}
